import { Component, OnInit } from '@angular/core';
import { LICENSE_KEY } from '../../license';
import { ProjectsService } from '../projects.service';
import { ExperiencesService } from '../experiences.service';
import { Observable } from 'rxjs';
import { formatDate } from '@angular/common';
import { SkillsService } from '../skills.service';
import { PublicationsService } from '../publications.service';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.scss'],
})
export class HomepageComponent implements OnInit {
  config: any;
  fullpage_api: any;

  projects: Observable<Object[]>;
  experiences: Observable<Object[]>;
  skills: Observable<Object[]>;
  publications: Observable<Object[]>;

  skillColorPalette: Array<String> = [
    '#5B2AD2', '#8864DF', '#360CA0', '#1D0556',
    '#5B2AD2', '#8864DF', '#360CA0', '#1D0556',
    '#E41887', '#EC5BAA', '#BC0067', '#660038',
    '#1C9EC9', '#58B9D9', '#046E92', '#023B4F'
  ];

  constructor(
    private projectsService: ProjectsService, 
    private experiencesService: ExperiencesService,
    private skillsService: SkillsService,
    private publicationsService: PublicationsService
    ) {
      this.projects = this.projectsService.fetchProjectsList();
      this.experiences = this.experiencesService.fetchExperiencesList();
      this.skills = this.skillsService.fetchSkillsList();
      this.publications = this.publicationsService.fetchPublicationsList(); // TODO fix fetch homepage

      this.config = {
        licenseKey: LICENSE_KEY,
        anchors: [
          'top',
          'introduction',
          'experience',
          'skills',
          'portfolio',
          'publications'
        ],

        scrollOverflow: true,

        menu: '#menu'
      };
  }

  ngOnInit(): void {
    
  }

  ngAfterViewInit(): void {
    this.projects.subscribe(_ => {
      this.fullpage_api.build();
    });

    this.experiences.subscribe(_ => {
      this.fullpage_api.build();
    });

    this.skills.subscribe(_ => {
      this.fullpage_api.build();
    })

    this.publications.subscribe(x => {
      console.warn(x);
      this.fullpage_api.build();
    });
  }

  getRef(fullPageRef) {
    this.fullpage_api = fullPageRef;
  }

  renderDate(date) {
    return date === null ? 'Present' : formatDate(date, 'MMMM yyyy', 'en-US', '+0430');
  }
}